    margin: 0;
  }

  .review {
    grid-area: 1 / 2 / 1 / 2;
  }

  .review__items {
    flex-flow: column;
    width: 33.75rem;
    display: flex;
  }

  .review__item {
    flex-flow: row-reverse;
    justify-content: start;
